/* Para que SASS sepa que variables.scss no es un archivo solo, sino que es soportado por app.scss, simplemente le podremos guion bajo (_) al comienzo del nombre. Y desde el archivo principal "app.scss" creamos un @import + el nombre del archivo entre comillas simples o dobles, no es necesario especificar el scss en el.

-Sucederá que si ejecutamos el watch, el apunta a app.scss y no a variables, entonces lo que hacemos es en glupfile cambiar la sentencia del watch por el signo * que indica (a todos), es decir que aplique los cambios a todos los archivos dentro de la carpeta sass.
*/
/* -Es una especie de "funcion" exlusiva de SASS (no existe en CSS nativo), y permite añadir multiples instrucciones y ejecutarlas; se pueden añadir paramentros (), si fuera necesario, o solo tener corchetes. */
/* - Ejemplo básico de funcionamiento de un Mixing

@mixin nuevoMixin($transform, $size)
{
    font-size: $size;
    color: $morado;
    text-transform: $transform;
}

  -Ahora aplicaremos esto para crear los Media Queries, ya que como explicamos no puede codificarse dentro del elemento que se desea aplicar. Haremos para que se adapte a cada dispositivo que lo ejecute.
*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.bg-verde {
  background-color: #4CB8B3;
}

.bg-amarillo {
  background-color: #FEF46D;
}

.header {
  background-color: #4CB8B3;
}
.header .contenido-header {
  padding: 2.5rem 0;
  /* display: flex;
  justify-content: space-between;
  align-items: center; */
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: #FFF;
  margin-bottom: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem #444;
}

.navegacion-principal {
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.navegacion-principal a {
  color: #FFF;
  font-size: 2rem;
}
.navegacion-principal a:hover {
  color: #FEF46D;
}

/* Aclaraciones:
1-Gradiente: Antes usabamos algo como añadir una imagen con cierto degradado y la superponiamos sobre la imagen o video para lograr el efecto. Hoy eso no se usa ya que con CSS podemos obtener el efecto.
 -Basicamente añadiendo al background "linear-gradient" y pasando el sentido del gradiente con "to" + ubicacion (top/button/right/left) + colores, logramos el efecto. Y en los colores podemos añadir los % de cada uno.
 -Si quisieramos darle sentido oblicuo o en algun angulo especifico reemplazamos el (to + ubicacion) por el gradoº. Ej: 45deg.

 -Por ultimo; si quieres lograr un efecto mas complejo y visible antes de aplicarlo en el CSS podemos usar paginas alternativas como (colorzilla.com) para elegir colores y efectos de gradientes y pegarlos en nuestro proyecto.
 -Aspectos de uso: Veremos flechas arriba y abajo, en los extremos y al medio. Para crear la del proyecto, las flechas del medio que son 2, las arrastramos fuera para quitarlas. Nos quedamos con la de los extremos, en la flecha de abajo elegimos el color, y en la de arriba la opacidad/transparencia. Por ultimo, tocamos de nuevo al medio para agregar un color mas.
 Por ultimo le damos la orientacion, en este caso "diagonal derecha".

 2- z-index: Posiciona la hoja o el orden en el que se muestre en pantalla. Aclaracion del practico; del titulo veremos que si queremos seleccionar el texto no lo permite, eso es porque la clave .overlay esta por delante de .contenido-video (que tiene el titulo), osea esta es hija de overlay que es quien tiene a su vez el video y el gradiente. Entonces para que el titulo quede delante tenemos que definir: position:relative + z-index: (Indicando la posicion), en este caso le damos 1. Es como la posicion de un objeto en una hoja de Canva.

*/
.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+1,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(137, 28, 33, 0.8) 1%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(137, 28, 33, 0.8) 1%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(137, 28, 33, 0.8) 1%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  width: 100%;
}

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  /* 2 */
  position: relative;
  z-index: 1;
}
.contenido-video h2 {
  display: flex;
  flex-direction: column;
  color: #FFF;
  font-size: 7rem;
  align-items: flex-start;
}
.contenido-video h2 span {
  background-color: #F53756;
  font-size: 2rem;
  padding: 1rem 2rem;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    /* display: grid;
       grid-template-columns: 1fr 1fr;

       column-gap: $separacion; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenido-festival h2 {
  font-size: 3rem;
}
.contenido-festival .fecha {
  color: #F53756;
  font-weight: 700;
}
.contenido-festival .texto {
  line-height: 1.5;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFF;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor .escenario {
  padding-top: 2.5rem;
}
.escenarios-contenedor .nombre-escenario {
  color: #000;
  font-size: 2rem;
  font-weight: 300;
}
.escenarios-contenedor .nombre-escenario:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.edm .nombre-escenario:after {
  background-image: url(../../src/img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../../src/img/icono_rock.png);
}

.calendario {
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-verde .calendario li:nth-child(2n+1) {
  background-color: #3b9691;
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #fef03a;
}

.galeria {
  padding: 2.5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  padding: 0;
  list-style: none;
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.5rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2.5rem;
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background-color: #FFF;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 3rem;
  border-radius: 50%;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem;
}
.boletos h3 {
  text-align: center;
  color: #FFF;
}
.boletos .contenedor-boletos {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
.boletos .plan p {
  color: #FFF;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pase {
  width: 100%;
  border-spacing: 0;
}
.pase thead {
  background-color: #F53756;
}
.pase thead th {
  padding: 1.25rem;
  color: #FFF;
  text-transform: uppercase;
}
.pase tbody {
  background-color: #FFF;
}
.pase tbody td {
  padding: 1.25rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase tbody tr:last-of-type td {
  border: none;
}
.pase tfoot {
  background-color: #752F97;
}
.pase tfoot td {
  padding: 1.25rem;
  font-size: 3rem;
  text-align: center;
  color: #FFF;
}

.footer {
  background-color: #F53756;
  padding: 1.25rem;
  text-align: center;
}
.footer p {
  color: #FFF;
  margin: 0;
}

